import { Component, NgZone, ElementRef, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { AdminPrivilegeProvider } from '../../providers/admin-privilege/admin-privilege';
import { ToastProvider } from '../../providers/toast';
import { LoaderProvider } from '../../providers/loader';
import { EventsService } from '../../providers/event.service';
import { Observable } from 'rxjs';
import { Trucks } from '../../providers/truck';
import { MapsAPILoader } from '@agm/core';
import { HomePage } from '../home/home';
import { CommonHelper } from '../../helpers/common';

/**
 * Generated class for the EditTransportDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-transport-details',
  templateUrl: 'edit-transport-details.html',
})
export class EditTransportDetailsPage {

  acountNo = false;
  details;
  editForm: FormGroup;
  address: FormArray;
  route_desired: FormArray;

  transportFlag = true;
  bankFlag = false;
  addressFlag = false;
  locationFlag = false;
  routeFlag = false;


  address_location = {
    type : "Point",
    coordinates: []
  }

  formValues;
  submitted = false;

  DestinationsArray = [];
  getTo = "";

  @ViewChild("searchTo")
  public searchElementRefTo: ElementRef;

  @ViewChild("searchCity")
  public searchElementRefCity: ElementRef;

  @ViewChild("searchStatue")
  public searchElementRefState: ElementRef;
  
  @ViewChild("search")
  public searchElementRef: ElementRef;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public admin: AdminPrivilegeProvider,
    public loader: LoaderProvider,
    public toast: ToastProvider,
    public events: EventsService,
    public trucks: Trucks,
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
    ) {
  
    this.details = this.navParams.get('data');

    let getLat = this.details.latitude;
    let getLng = this.details.longitude;
    localStorage.setItem('latlng', JSON.stringify({
      lat: getLat,
      lng: getLng
    }));

    var routeDesireds = [];
    this.details.route_desired.forEach(element => {
      routeDesireds.push(this.routeDesiredForm(element));
      var obj = {"location": element.location};
      this.DestinationsArray.push(obj);
    });
    this.editForm = this.fb.group({
      agency_name: [''],
      website: [''],
      transport_name: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      owner_name: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      // location: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      omobile_no_1: ['', Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(10)])],
      omobile_no_2: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
      contact_person: ['', Validators.compose([Validators.required, Validators.minLength(2)])],
      cmobile_no_1: ['', Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(10)])],
      cmobile_no_2: ['', Validators.compose([Validators.minLength(10), Validators.maxLength(10)])],
      account_holder_name: ['', Validators.compose([Validators.minLength(2), Validators.maxLength(25)])],
      account_number: ['', Validators.compose([Validators.maxLength(16)])],
      confirm_account_number: ['', Validators.compose([Validators.maxLength(16)])],
      ifsc: ['', Validators.compose([Validators.minLength(11), Validators.maxLength(11)])],
      address: this.fb.array([ this.createAddressObj(this.details.address[0])]),
      location_allotment: [''],
      location_restriction: [''],
      latitude: [''],
      longitude: [''],
      route_desired: this.fb.array(routeDesireds),
      location_to: ['']
    });

    
    this.editForm.controls['agency_name'].setValue(this.details.agency_name ? this.details.agency_name: "");
    this.editForm.controls['website'].setValue(this.details.website);
    this.editForm.controls['transport_name'].setValue(this.details.transport_name);
    this.editForm.controls['owner_name'].setValue(this.details.owner_name);
    this.editForm.controls['omobile_no_1'].setValue(this.details.omobile_no_1);
    this.editForm.controls['omobile_no_2'].setValue(this.details.omobile_no_2);
    this.editForm.controls['contact_person'].setValue(this.details.contact_person);
    this.editForm.controls['cmobile_no_1'].setValue(this.details.cmobile_no_1);
    this.editForm.controls['cmobile_no_2'].setValue(this.details.cmobile_no_2);
    this.editForm.controls['account_holder_name'].setValue(this.details.account_holder_name);
    this.editForm.controls['account_number'].setValue(this.details.account_number);
    this.editForm.controls['ifsc'].setValue(this.details.ifsc);
    this.editForm.controls['location_allotment'].setValue(this.details.location_allotment);
    this.editForm.controls['location_restriction'].setValue(this.details.location_restriction);
    this.editForm.controls['latitude'].setValue(this.details.latitude);
    this.editForm.controls['longitude'].setValue(this.details.longitude);

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditTransportDetailsPage');
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  getLocation(e){
    e.preventDefault();
    this.events.broadcast('location');
  }

  routeDesiredForm(e) {
    return this.fb.group({
      location: [e.location ? e.location : '']
    })
  }

  addToArray() {
    var obj = {"location": this.getTo};
    this.DestinationsArray.push(obj);
    this.editForm.get('location_to').setValue('');
}

removeToArray(index) {
  this.DestinationsArray.splice(index, 1);
  console.log("array >>>", this.DestinationsArray);
}

  addTo(): void {
    this.route_desired = this.editForm.get('route_desired') as FormArray;
    this.route_desired.push(this.routeDesiredForm(''));
  }

  removeTo(index) {
    if (index === 0) {
      this.toast.error({ 'message': 'Cannot remove this form.' });
    } else {
      this.route_desired = this.editForm.get('route_desired') as FormArray;
      this.route_desired.removeAt(index);
    }
  }

  confirmAccount(){
    if (this.editForm.value.account_number == this.editForm.value.confirm_account_number) {
      console.log("Same");
      this.acountNo = false;
    } else {
      console.log("Not Same");
      this.acountNo = true;
    }
  }

  createAddressObj(add): FormGroup  {
    return this.fb.group({
      address_line_1: [add.address_line_1, Validators.compose([Validators.required, , Validators.minLength(2)])],
      address_line_2: [add.address_line_2, Validators.compose([Validators.required, , Validators.minLength(2)])],
      city: [add.city, Validators.compose([Validators.required, , Validators.minLength(2)])],
      state: [add.state, Validators.compose([Validators.required, , Validators.minLength(2)])],
      pincode: [add.pincode, Validators.compose([Validators.required, Validators.minLength(6), Validators.maxLength(6)])]
    })
  }

  // Get controls for address form array
  get addressForm() {
    return this.editForm.get('address')['controls'][0].controls;
  }

  // Google map address
  addressData(e: any):void {
    this.formValues = this.editForm.value;
    
    this.addressForm['address_line_1'].setValue(e.address_line1);
    this.addressForm['address_line_2'].setValue(e.address_line2);
    this.addressForm['city'].setValue(e.city);
    this.addressForm['state'].setValue(e.state);
    this.addressForm['pincode'].setValue(e.pincode);

    this.address_location.coordinates = [];
    this.address_location.coordinates.push(e.lng);
    this.address_location.coordinates.push(e.lat);
    this.formValues.address[0].address_location = this.address_location;
    console.log(">>>FormVal1", this.formValues);
  }

  update(isValid, value) {
    console.log(">>>FormVal2", this.editForm);
    this.submitted = true;
    if(isValid) {
      this.formValues = value;
      console.log(">>>FormVal2", this.formValues);
      this.formValues.address[0].address_line_1 = value.address[0].address_line_1;
      this.formValues.address[0].address_line_2 = value.address[0].address_line_2;
      this.formValues.address[0].city = value.address[0].city;
      this.formValues.address[0].state = value.address[0].state;
      this.formValues.address[0].pincode = value.address[0].pincode;
      this.formValues.address[0].address_location = this.address_location;
      this.formValues.route_desired = this.DestinationsArray;

      this.formValues.agency_name = this.formValues.agency_name.agency_name ? this.formValues.agency_name.agency_name: this.formValues.agency_name

      this.loader.show();
      this.admin.updateTransportList(this.details._id, this.editForm.value).subscribe((res: any) => {
        this.loader.hide();
        this.toast.show({ 'message': 'Updated successfully.' });
        this.navCtrl.setRoot('ManageTransportPage');
        console.log(">>>>>res", res);
      }, err => {
        this.loader.hide();
        this.toast.error({ 'message': 'Something went wrong.' });
        console.log(">>>>>err", err);
      });

    }
  }

  // Search query for Agency Name list
  agencyName = (keyword: any): Observable<any[]> => {
    if (keyword) {
      // getTruckPositionList
      return this.trucks.searchAgencyName(keyword).map((res: any) => {
        return res.json();
      });
    } else {
      return Observable.of([]);
    }    
  }

  
  preTransport() {
    this.transportFlag = true;
    this.bankFlag = false;
    this.addressFlag = false;
    this.locationFlag = false;
    this.routeFlag = false;
  }

  nextBank() {
    this.transportFlag = false;
    this.bankFlag = true;
    this.addressFlag = false;
    this.locationFlag = false;
    this.routeFlag = false;
  }

  nextAddress() {
    this.transportFlag = false;
    this.bankFlag = false;
    this.addressFlag = true;
    this.locationFlag = false;
    this.routeFlag = false;
    setTimeout(() => {
      this.mapsAPILoader.load().then(() => {
        let presentCity = new google.maps.places.Autocomplete(this.searchElementRefCity.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentCity.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentCity.getPlace();
            let components = CommonHelper.extract_components(place);
            let pre_city = CommonHelper.get_sublocality(components);
            console.log(components, pre_city);
            
            (<HTMLInputElement>document.getElementById('City')).value = pre_city;
            let addr = this.editForm.get('address') as FormArray;
            let first = addr.at(0);
            first.get('city').setValue(pre_city);
          });
        });

        let presentState = new google.maps.places.Autocomplete(this.searchElementRefState.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentState.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentState.getPlace();
            let components = CommonHelper.extract_components(place);
            
            (<HTMLInputElement>document.getElementById('State')).value = components.administrative_area_level_1;
            let addr = this.editForm.get('address') as FormArray;
            let first = addr.at(0);
            first.get('state').setValue(components.administrative_area_level_1);
          });
        }); 
      });
    }, 2000);
  }

  nextLocation() {
    this.transportFlag = false;
    this.bankFlag = false;
    this.addressFlag = false;
    this.locationFlag = true;
    this.routeFlag = false;


    // let getLat = this.details.latitude;
    // let getLng = this.details.longitude;
    // localStorage.setItem('latlng', JSON.stringify({
    //   lat: getLat,
    //   lng: getLng
    // }));
    // this.events.broadcast('location');

    setTimeout(() => {
      this.mapsAPILoader.load().then(() => {
        let presentCity = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
          componentRestrictions: {"country": "IN"},
          types: []
        });
        presentCity.addListener("place_changed", () => {
          this.ngZone.run(() => {
            //get the place result
            let place: google.maps.places.PlaceResult = presentCity.getPlace();
            let lat = place.geometry.location.lat();
            let lng = place.geometry.location.lng();
            localStorage.setItem('latlng', JSON.stringify({
              lat: lat,
              lng: lng
            }));
            this.events.broadcast('location');
            let components = CommonHelper.extract_components(place);
            let pre_city = CommonHelper.get_sublocality(components);
            console.log(components, pre_city);

            (<HTMLInputElement>document.getElementById('Location')).value = pre_city;
            this.editForm.controls['location_allotment'].setValue(pre_city);

            this.editForm.controls['latitude'].setValue(lat);
            this.editForm.controls['longitude'].setValue(lng);
          });
        });
    
      });
    }, 1000);
  }

  nextRoute() {
    this.transportFlag = false;
    this.bankFlag = false;
    this.addressFlag = false;
    this.locationFlag = false;
    this.routeFlag = true;

    setTimeout(() => {
      let toSearch = new google.maps.places.Autocomplete(this.searchElementRefTo.nativeElement, {
        componentRestrictions: {"country": "IN"},
        types: []
      });
      toSearch.addListener("place_changed", () => {
        this.ngZone.run(() => {
          //get the place result
          let place: google.maps.places.PlaceResult = toSearch.getPlace();
          let components = CommonHelper.extract_components(place);
          let pre_city = CommonHelper.get_sublocality(components);
          console.log(components, pre_city);

          (<HTMLInputElement>document.getElementById('TO')).value = pre_city;
          this.getTo = pre_city;                
        });
      });
    }, 2000);
  }

}



